import React, {useEffect, useState } from "react";
import NewsItem from "./NewsItem";
import Spinner from "./Loader";
import PropTypes from "prop-types";
import InfiniteScroll from "react-infinite-scroll-component";

const News = (props) => {
  
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [totalResult, setTotalResult] = useState(0);
 
  const updateNews = async ()=> {
    props.setProgress(70);
    const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=dbe57b028aeb41e285a226a94865f7a7&page=${page}&pageSize=${props.pageSize}`;
    let data = await fetch(url);
    let parsedData = await data.json();
    props.setProgress(90);
    setArticles(parsedData.articles);
    setTotalResult(parsedData.totalResults)
    setLoading(false)
    props.setProgress(100);
  }

  useEffect(() => {
    updateNews();
    document.title = `NewsMonkey-${props.category}`;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchMoreData = async () => {
    const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=d093053d72bc40248998159804e0e67d&page=${page+1}&pageSize=${props.pageSize}`;
    setPage(page + 1);
    let data = await fetch(url);
    let parsedData = await data.json();
    setArticles(articles.concat(parsedData.articles));
    setTotalResult(parsedData.totalResults)
  };
  // console.log(articles);

    return (
      <>
        <h1
          className="text-center my-5 pt-5"
          style={{ textTransform: "capitalize" }}
        >
          {props.category}-Top Headings
        </h1>
        {loading && <Spinner />}

        <InfiniteScroll
         dataLength={articles.length}
          next={fetchMoreData}
          hasMore={articles.length !== totalResult}
          loader={<Spinner />}
        >
          <div className="container">
            <div className="row">
              {articles.map((item) => {
                console.log(item.category);
                return (
                  <div
                    className="col-lg-3 col-md-4 col-sm-6 my-3"
                    key={item.url}
                  >
                    {" "}
                    <NewsItem
                      urlToImage={item.urlToImage}
                      url={item.url}
                      title={item.title}
                      description={item.description}
                      author={item.author}
                      publishedAt={item.publishedAt}
                      source={item.source}
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </InfiniteScroll>
      </>
    );
}

News.defaultProps = {
  country: "us",
  pageSize: 8,
  category: "genaral",
};
News.propTypes = {
  country: PropTypes.string,
  pageSize: PropTypes.number,
  category: PropTypes.string,
};
export default News